.exercise {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__header {
    display: flex;
    flex-direction: row-reverse;

    height: 35px;

    .icon {
      @include icon('cross');
      color: #404040;
    }

    &-text {
      text-align: center;
    }
  }

  &__image {
    display: flex;
    margin: auto;
  }

  &__timer {
    display: flex;
    width: 50%;
    margin: auto;
  }

  &__controls {
    display: flex;
    align-items: center;
    bottom:0;
    right: 0;
    position:fixed;
    z-index:150;
    height:50px;
    width: 100%;
    background: rgba(62, 152, 199, 0.2);

    .control-panel {
      display: flex;
      justify-content: space-around;
      font-size: 24px;
      width: 100%;
      color: #3e98c7;
      &__play {
        @include icon('play');
      }

      &__pause {
        @include icon('pause');
      }

      &__next {
        @include icon('next');
      }
    }
  }
}
